import React from 'react';
import HeaderContainer from './styled';
import ChangeText from '../ChangeText';

const texts = ["", "", " ", " "];

export default class Header extends React.Component {
  render () {
    return (
      <HeaderContainer>
        <h1>
             <br />
          <span><ChangeText texts={texts} /></span>
        </h1>
        <p>
           ,    ,  &nbsp;&nbsp;
        </p>
      </HeaderContainer>
    );
  }
}



